<template>
  <textarea type="text"
            rows="5"
            v-bind:value="formatted"
            v-bind:placeholder="placeholder"
            v-on:change="onChange($event.target.value)"></textarea>
</template>

<script>
import { format, modelize, getPlaceholder } from './mappers'

export default {
  name: 'UrlTextarea',
  props: ['name', 'type', 'value'],
  methods: {
    onChange: function (value) {
      const output = modelize(this.type, value)

      this.$emit('input', output)
    }
  },
  computed: {
    formatted: function () {
      return format(this.type, this.value)
    },
    placeholder: function () {
      return getPlaceholder(this.type)
    }
  }
}
</script>

<style scoped>
  ::-webkit-resizer {
    display: none !important;
  }

  textarea {
    border: 0;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 100%;
    font-weight: 500;
    font-size: 13pt;
    line-height: 32px;
    font-family: 'Source Sans Pro';
    background-color: transparent;
  }

  textarea:focus {
    outline-color: #a58d52;
  }
</style>



