<template>
  <section class="basics">
    <div class="col-group">
      <entry v-bind:label="english ? 'Issue location' : 'Miejscowość'">
        <url-input name="location"
                   v-bind:default="english ? 'Gliwice, Poland' : 'Gliwice'" />
      </entry>

      <entry v-bind:label="english ? 'Date of issue' : 'Data wystawienia'">
        <url-input name="issuedAt"
                   type="date"
                   v-bind:default="defaults.issuedAt" />
      </entry>

      <entry v-bind:label="english ? 'Date of sale' : 'Data sprzedaży'">
        <url-input name="provisionedAt"
                   type="date"
                   v-bind:default="defaults.provisionedAt" />
      </entry>

      <entry v-bind:label="english ? 'Exchange rate' : 'Kurs wymiany'">
        <div class="exchange-rate-container">
          <div class="exchange-rate-button" v-on:click="updateExchangeRate">
            <i class="fas fa-sync-alt" v-bind:class="{ 'rotate-center': isFetching }"></i>
          </div>
          <url-input name="exchangeRate"
                     type="number"
                     v-bind:default="defaultExchangeRate" />
        </div>
      </entry>
    </div>
  </section>
</template>

<script>
import moment from 'moment'
import axios from 'axios'

const queryDateToMoment = string => {
  // todo: validate for format, or is it already
  //       validated in input?

  const parts = string.split('-')

  return parts.length === 3 ? moment({ date: parts[0], month: parts[1] - 1, year: parts[2] }) : null
}

export default {
  name: 'Basics',
  props: ['english', 'defaultExchangeRate'],
  data: function () {
    const expectedAt = moment().set({ date: 20 })

    return {
      isFetching: false,
      defaults: {
        issuedAt: moment(expectedAt),
        provisionedAt: moment(expectedAt).add(1, 'months').date(1).subtract(1, 'days')
      }
    }
  },
  computed: {
    earlierAt: function () {
      const { query } = this.$route

      const issuedAt = query.issuedAt ? queryDateToMoment(query.issuedAt) : this.defaults.issuedAt
      const provisionedAt = query.provisionedAt ? queryDateToMoment(query.provisionedAt) : this.defaults.provisionedAt

      if (issuedAt && provisionedAt) {
        return issuedAt.isBefore(provisionedAt) ? issuedAt : provisionedAt
      } else {
        return moment()
      }
    },
    exchangeRateAt: function () {
      return moment(this.earlierAt).subtract(1, 'day')
    }
  },
  methods: {
    updateExchangeRate: function () {
      const FORMAT = 'YYYY-MM-DD'

      const from = moment(this.exchangeRateAt).subtract(1, 'month').format(FORMAT)
      const to = this.exchangeRateAt.format(FORMAT)

      this.isFetching = true

      axios
        .get(`http://api.nbp.pl/api/exchangerates/rates/a/eur/${from}/${to}/?format=json`)
        .then(({ data }) => {
          const rate = data.rates[data.rates.length - 1]

          this.$router.push({ query: { ...this.$route.query, exchangeRate: rate.mid } })
          this.isFetching = false
        })
        .catch(() => {
          this.isFetching = false
        })
    }
  }
}
</script>

<style scoped>
  @media print {
    .exchange-rate-button {
      display: none;
    }
  }

  .exchange-rate-container {
    position: relative;
  }

  .exchange-rate-button {
    position: absolute;
    left: -30px;
    top: 8px;
    cursor: pointer;
    z-index: 50;
  }

  .exchange-rate-button i {
    opacity: 0.25;
    transition: 0.25s;
  }

  .exchange-rate-button:hover i {
    opacity: 1;
  }

  .exchange-rate-button i.rotate-center {
    opacity: 1;
    animation: rotate-center 2s linear infinite both;
  }

  @-webkit-keyframes rotate-center {
    0% { transform: rotate(0); }
    100% { transform: rotate(360deg); }
  }

  @keyframes rotate-center {
    0% { transform: rotate(0); }
    100% { transform: rotate(360deg); }
  }
</style>



