import shortid from 'shortid'
import moment from 'moment'

const timespan = moment().format('MM-YYYY')

const defaults = {
  name: `Software maintenance and support services: ${timespan}

Programowanie i wsparcie techniczne: ${timespan}`,
  quantity: 174,
  unit: 'hours',
  netPrice: { amount: 10, currency: 'EUR' },
  vatRate: 0
}

const create = (order, fields = {}) => ({
  id: shortid.generate(),
  order,
  ...defaults,
  ...fields,
  netPrice: {
    ...defaults.netPrice,
    ...fields.netPrice
  }
})

const querizeParameter = (queries, record, field) => ({
  ...queries,
  [`${field}Record${record.order}`]: record[field]
})

const querizeNetPrice = (queries, record, field) => ({
  ...queries,
  [`netPrice${field}Record${record.order}`]: record.netPrice[field.toLowerCase()]
})

const basicFields = ['name', 'quantity', 'unit', 'vatRate']
const netPriceFields = ['Amount', 'Currency']

const querize = record => {
  let queries = {}

  queries = querizeParameter(queries, record, 'name')
  queries = querizeParameter(queries, record, 'quantity')
  queries = querizeParameter(queries, record, 'unit')
  queries = querizeParameter(queries, record, 'vatRate')

  queries = querizeNetPrice(queries, record, 'Amount')
  queries = querizeNetPrice(queries, record, 'Currency')

  return queries
}

const dequerizeDefaults = query => {
  return Object.keys(query).reduce((dequerized, field) => {
    const value = query[field]

    if (field.search('Record') >= 0) {
      /* eslint-disable */

      if (field.search('name') >= 0 && value == defaults.name) return dequerized
      if (field.search('quantity') >= 0 && value == defaults.quantity) return dequerized
      if (field.search('unit') >= 0 && value == defaults.unit) return dequerized
      if (field.search('vatRate') >= 0 && value == defaults.vatRate) return dequerized

      if (field.search('Amount') >= 0 && value == defaults.netPrice.amount) return dequerized
      if (field.search('Currency') >= 0 && value == defaults.netPrice.currency) return dequerized

      /* eslint-enable */
    }

    return { ...dequerized, [field]: value }
  }, {})
}

const numberToArray = amount => [...Array(amount).keys()].map(i => i + 1)

const modelize = query => {
  const amount = +(query.records) || 1

  const records = numberToArray(amount).map(order => {
    let fields = { netPrice: {} }

    basicFields.forEach(field => {
      const value = query[`${field}Record${order}`]
      if (value) fields[field] = value
    })

    netPriceFields.forEach(field => {
      const value = query[`netPrice${field}Record${order}`]
      if (value) fields.netPrice[field.toLowerCase()] = value
    })

    return create(order, fields)
  })

  return records
}

export {
  create,
  querize,
  dequerizeDefaults,
  modelize
}



