<template>
  <header>
    <document-text class="big">
      <span style="margin-left: -1px">{{ english ? 'Invoice' : 'Faktura VAT' }}</span>
      <url-input class="input-big"
                 name="title"
                 v-bind:default="defaults.title" />
    </document-text>

    <document-text class="small">
      <url-input class="input-small"
                 name="subtitle"
                 v-bind:default="defaults.subtitle" />
    </document-text>
  </header>
</template>

<script>
import moment from 'moment'

export default {
  name: 'Titles',
  props: ['english'],
  data: function () {
    return {
      defaults: {
        title: `FV ${moment().format('M/YYYY')}`,
        subtitle: this.english ? 'Original' : 'Kopia'
      }
    }
  }
}
</script>

<style scoped>
  header {
    display: flex;
    flex-direction: column;
  }

  .small {
    margin-top: 10px;
  }

  .input-big {
    width: 300px;
    display: inline;
    line-height: 32px;
    font-size: 22pt;
    letter-spacing: 4px;
    font-family: 'Montserrat';
  }

  .input-small {
    display: inline;
    line-height: 32px;
    font-family: 'Montserrat';
    font-weight: 400;
    letter-spacing: 5px;
    font-size: 12px;
    text-transform: uppercase;
  }
</style>



