<template>
  <section class="subjects">

    <subjects-table v-bind:english="english"
                    v-bind:records="records"
                    v-bind:currency-settings="currencySettings"
                    v-bind:exchange-rate="exchangeRate"
                    v-bind:summary="summary"
                    v-on:record-add="addRecord"
                    v-on:record-remove="removeRecord"></subjects-table>

    <space class="medium" />

    <subjects-summary v-bind:english="english"
                      v-bind:summary="summary"
                      v-bind:currency-settings="currencySettings"
                      v-bind:exchange-rate="exchangeRate"
                      v-bind:is-price-in-PLN="records[0].netPrice.currency === 'zł'"></subjects-summary>

  </section>
</template>

<script>
import { create, querize, dequerizeDefaults, modelize } from './table/record/domain'

import SubjectsTable from './table'
import SubjectsSummary from './summary'

export default {
  name: 'Subjects',
  props: ['english', 'defaultExchangeRate'],
  components: {
    SubjectsTable,
    SubjectsSummary
  },
  data: function () {
    return {
      records: modelize(this.$route.query)
    }
  },
  watch: {
    records: {
      handler: function (records) {
        const base = { records: records.length }
        const queries = records.reduce((queries, record) => ({ ...queries, ...querize(record) }), base)

        let dequerized = dequerizeDefaults({ ...this.$route.query, ...queries })

        if (dequerized.records === 1) delete dequerized.records

        this.$router.push({ query: dequerized })
      },
      deep: true
    }
  },
  methods: {
    addRecord: function () {
      this.records.push(create(this.records.length + 1))
    },
    removeRecord: function () {
      this.records = this.records.filter((x, i) => i !== this.records.length - 1)
    }
  },
  computed: {
    exchangeRate: function () {
      return this.$route.query.exchangeRate || (+this.defaultExchangeRate)
    },
    summary: function () {
      return this.records.reduce((summary, record) => {
        const netValue = record.quantity * record.netPrice.amount
        const vatValue = netValue * record.vatRate
        const grossValue = netValue + vatValue

        return {
          netValue: summary.netValue + netValue,
          vatValue: summary.vatValue + vatValue,
          grossValue: summary.grossValue + grossValue
        }
      }, { netValue: 0, vatValue: 0, grossValue: 0 })
    },
    currencySettings: function () {
      const isPriceInPLN = this.records[0].netPrice.currency === 'zł'

      return {
        symbol: isPriceInPLN ? 'zł' : 'EUR',
        thousandsSeparator: ' ',
        fractionCount: 2,
        fractionSeparator: isPriceInPLN ? ',' : '.',
        symbolPosition: 'end',
        symbolSpacing: true
      }
    }
  }
}
</script>

<style scoped>
  .subjects {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
</style>



