<template>
  <section class="record">
    <div>{{ record.order }}.</div>
    <div>
      <document-textarea class="record-textarea"
                         v-model="record.name"
                         v-bind:name="'record' + record.order + 'ServiceName'" />
    </div>
    <div>
      <document-input class="record-input text-right"
                      v-model="record.quantity"
                      v-bind:name="'record' + record.order + 'Quantity'" />
    </div>
    <div>
      <document-input class="record-input"
                      v-model="record.unit"
                      v-bind:name="'record' + record.order + 'Unit'" />
    </div>
    <div>
      <document-input class="record-input"
                      type="currency"
                      v-model="record.netPrice"
                      v-bind:name="'record' + record.order + 'NetPrice'" />
    </div>
    <div>{{ netValue | currency(currencySettings) }}</div>
    <div>
      <document-input class="record-input"
                      type="percentage"
                      v-model="record.vatRate"
                      v-bind:name="'record' + record.order + 'VatRate'" />
    </div>
    <div>{{ vatValue | currency(currencySettings) }}</div>
    <div>{{ grossValue | currency(currencySettings) }}</div>
  </section>
</template>

<script>
export default {
  name: 'Record',
  props: ['english', 'record', 'currencySettings'],
  computed: {
    netValue: function () {
      return this.record.quantity * this.record.netPrice.amount
    },
    vatValue: function () {
      return this.record.vatRate * this.netValue
    },
    grossValue: function () {
      return this.netValue + this.vatValue
    }
  }
}
</script>

<style scoped>
  @media print {
    .record-textarea {
      resize: none;
    }
  }

  .record {
    display: flex;
    font-family: 'Lato';
    font-weight: 400;
    border-bottom: 1px solid #222;
    padding-bottom: 15px;
  }

  .record {
    padding-top: 15px;
    border-bottom-color: #ccc;
    opacity: 1;
    border-bottom-style: dashed;
  }

  .record:last-child {
    border-bottom-style: solid;
    border-bottom-color: #222;
  }

  .record-textarea {
    margin-top: -1px;
    line-height: 20px;
    max-width: 100%;
    display: block;
    height: 102px;
    overflow: hidden;
  }

  .record-input {
    line-height: 20px;
    font-weight: 400;
    margin-top: -1px;
  }

  .text-right {
    width: calc(100% - 5px);
    padding-right: 5px;
    text-align: right;
  }
</style>



