<template>
  <div></div>
</template>

<script>
export default {
  name: 'Row'
}
</script>

<style>
  .subjects .table .row > * {
    line-height: 20px;
    box-sizing: border-box;
    padding: 0 2px;
  }

  .subjects .table .row > *:nth-child(1) { width: 3%; padding-left: 0; }
  .subjects .table .row > *:nth-child(2) { width: 23%; }
  .subjects .table .row > *:nth-child(3) { width: 8%; }
  .subjects .table .row > *:nth-child(4) { width: 7%; }
  .subjects .table .row > *:nth-child(5) { width: 10%; }
  .subjects .table .row > *:nth-child(6) { width: 13%; }
  .subjects .table .row > *:nth-child(7) { width: 11%; }
  .subjects .table .row > *:nth-child(8) { width: 12%; }
  .subjects .table .row > *:nth-child(9) { width: 13%; padding-right: 0; }

  .subjects .table .row.aside > *:nth-child(1) { width: 41%; }
  .subjects .table .row.aside > *:nth-child(2) { width: 28%; }
  .subjects .table .row.aside > *:nth-child(3) { width: 31%; text-align: right; padding-right: 0; }
</style>



