<template>
  <section class="col-group sides">
    <div class="col-6">
      <document-text class="big">
        {{ english ? 'Seller' : 'Sprzedawca' }}:
      </document-text>

      <space />

      <div class="vertical">
        <document-text>
          <strong>Maciej Mazur Tu i Teraz</strong>
        </document-text>
        <document-text>
          <strong>ul. Perkoza 15/13</strong>
        </document-text>
        <document-text>
          <strong>44-100, Gliwice</strong>
        </document-text>
      </div>

      <space />

      <div class="col-group">
        <entry v-bind:label="english ? 'VAT ID' : 'NIP EU'">
          <url-input type="text" name="sellerId"
                     default="1234 4312 4325 5325 6234 PL" />
        </entry>

        <entry label="Bank">
          <url-input type="text" name="sellerBank"
                     default="ING Bank Śląski" />
        </entry>

        <entry v-bind:label="english ? 'IBAN' : 'Numer konta'">
          <url-input type="text" name="sellerBankAccount"
                     default="123 123 123 123 123 123" />
        </entry>

        <entry v-bind:label="english ? 'SWIFT code' : 'Kod SWIFT'">
          <url-input type="text" name="sellerSwift"
                     default="INGBPLPW" />
        </entry>
      </div>
    </div>

    <div class="col-6">
      <document-text class="big">{{ english ? 'Customer' : 'Nabywca' }}:</document-text>

      <space />

      <div class="vertical">
        <url-input type="text" name="buyerLine1"
                   v-bind:default="english ? 'Some company' : 'Jakaś firma'" />
        <url-input type="text" name="buyerLine2"
                   v-bind:default="english ? 'Some street' : 'Jakaś ulica'" />
        <url-input type="text" name="buyerLine3"
                   v-bind:default="english ? 'Some postcode, city, country' : 'Jakiś kod pocztowy, miasto, kraj'" />
      </div>

      <space />

      <div class="col-group">
        <entry v-bind:label="english ? 'VAT ID' : 'NIP EU'">
          <url-input type="text" name="buyerId"
                     default="1234 1234 1234 1234 1234 EU" />
        </entry>

        <entry v-bind:label="english ? 'Contact' : 'Kontakt'">
          <url-input type="text" name="buyerContact"
                     default="test@test.test" />
        </entry>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Sides',
  props: ['english']
}
</script>

<style scoped>
  .vertical {
    display: flex;
    flex-direction: column;
  }
</style>



