import Vue from 'vue'

import Text from './text'
import Space from './space'
import Entry from './entry'
import UrlInput from './input'
import UrlTextarea from './input/textarea'

import DocumentInput from './input/document'

Vue.component('document-text', Text)
Vue.component('space', Space)
Vue.component('entry', Entry)
Vue.component('url-input', UrlInput)
Vue.component('document-textarea', UrlTextarea)
Vue.component('document-input', DocumentInput)



