<template>
  <section class="table">

    <row-styles></row-styles>

    <header class="row header">
      <div>{{ english ? 'No.' : 'Lp.' }}</div>
      <div>{{ english ? 'Service name' : 'Usługa' }}</div>
      <div>{{ english ? 'Quantity' : 'Ilość' }}</div>
      <div>{{ english ? 'Unit' : 'J.m.' }}</div>
      <div>{{ english ? 'Net price' : 'Cena netto' }}</div>
      <div>{{ english ? 'Net value' : 'Wartość netto' }}</div>
      <div>{{ english ? 'VAT rate' : 'Stawka VAT' }}</div>
      <div>{{ english ? 'VAT value' : 'Wartość VAT' }}</div>
      <div>{{ english ? 'Gross value' : 'Wartość brutto' }}</div>
    </header>

    <section class="records">
      <record class="row"
              v-for="record in records"
              v-bind:key="record.id"
              v-bind:record="record"
              v-bind:currency-settings="currencySettings"
              v-bind:english="english"></record>
    </section>

    <footer class="row footer">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div>{{ english ? 'total' : 'razem' }}:</div>
      <div>{{ summary.netValue | currency(currencySettings) }}</div>
      <div></div>
      <div>{{ summary.vatValue | currency(currencySettings) }}</div>
      <div>{{ summary.grossValue | currency(currencySettings) }}</div>
    </footer>

    <aside class="row aside">
      <div>
        <section class="menu">
          <div class="button" v-on:click="$emit('record-add')">
            <i class="fa fa-plus"></i>
            <span>dodaj</span>
          </div>
          <div class="button" v-on:click="$emit('record-remove')">
            <i class="fa fa-minus"></i>
            <span>skróć</span>
          </div>
        </section>
      </div>
      <div>{{ english ? 'value of VAT in PLN:' : 'wartość VAT w PLN:' }} {{ vatInPLN | currency(polishCurrencySettings) }}</div>
      <div>reverse charge / odwrotne obciążenie</div>
    </aside>

  </section>
</template>

<script>
// import converters from './converters'
import Record from './record'
import RowStyles from './row'

export default {
  name: 'SubjectsTable',
  props: ['english', 'records', 'currencySettings', 'exchangeRate', 'summary'],
  components: {
    Record,
    RowStyles
  },
  computed: {
    vatInPLN: function () {
      return this.exchangeRate * this.summary.vatValue
    },
    polishCurrencySettings: function () {
      return {
        ...this.currencySettings,
        symbol: 'zł',
        fractionSeparator: ','
      }
    }
  }
}
</script>

<style scoped>
  @media print {
    .menu {
      display: none !important;
    }
  }

  .subjects {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  .row,
  .header,
  .record {
    display: flex;
    font-family: 'Lato';
    font-weight: 400;
    border-bottom: 1px solid #222;
    padding-bottom: 15px;
  }

  .header {
    white-space: nowrap;
    color: #aaa;
  }

  .footer {
    padding-top: 15px;
    border-bottom: 0;
  }

  .footer > *:nth-child(5) {
    color: #aaa;
  }

  .aside {
    color: #aaa;
    border-bottom: 0;
  }

  .menu {
    display: flex;
    opacity: 0;
    transition: 0.25s;
  }

  .menu .button {
    display: flex;
    font-family: 'Montserrat';
    font-weight: 300;
    font-size: 12px;
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 24px;
    margin-right: 20px;
    align-items: center;
    background-color: #666;
    color: white;
    padding: 5px 10px;
    border-radius: 2px;
    margin-top: -14px;
    transition: 0.25s;
    cursor: pointer;
  }

  .menu .button:hover {
    background-color: #222;
  }

  .menu .button i {
    margin-right: 10px;
  }

  .table:hover .menu {
    opacity: 1;
  }
</style>



