<template>
  <section class="col-group">
    <div class="col-12">
      <document-text class="big">
        <span>{{ english ? 'Total value' : 'Razem' }}:</span>
        <strong>{{ summary.grossValue | currency(currencySettings) }}</strong>
      </document-text>
    </div>

    <div class="col-12">
      <document-text>
        <span v-bind:style="{ 'padding-left': english ? '133px' : '61px' }">{{ english ? 'In words' : 'Słownie' }} :</span>
        <span style="padding-left: 12px">{{ priceVerbally | capitalize }}.</span>
      </document-text>
    </div>

    <div class="col-12" v-bind:title="exchangeRate">
      <document-text class="small">
        <span style="opacity: 0.25"
              v-bind:style="{ 'padding-left': english ? '218px' : '143px' }">{{ zlotowki | currency(polishCurrencySettings) }}</span>
      </document-text>
    </div>
  </section>
</template>

<script>
import converters from './converters'

const getDecimal = split => {
  if (!split) return null
  if (split.length === 1) return split * 10
  return +split.substr(0, 2)
}

export default {
  name: 'SubjectsSummary',
  props: ['english', 'summary', 'exchangeRate', 'isPriceInPLN'],
  computed: {
    currencySettings: function () {
      return {
        symbol: this.isPriceInPLN ? 'zł' : 'EUR',
        thousandsSeparator: ' ',
        fractionCount: 2,
        fractionSeparator: this.isPriceInPLN ? ',' : '.',
        symbolPosition: 'end',
        symbolSpacing: true
      }
    },
    polishCurrencySettings: function () {
      return {
        ...this.currencySettings,
        symbol: 'zł',
        fractionSeparator: ','
      }
    },
    priceVerbally: function () {
      const converter = this.english ? converters.en : converters.pl

      const string = `${Math.round(this.summary.grossValue * 100) / 100}`
      const splits = string.split('.')

      const euro = splits[0]
      const cent = getDecimal(splits[1])

      const translations = {
        joiner: this.english ? 'and' : 'i',
        smaller: this.isPriceInPLN ? 'złotych' : (this.english ? 'cents' : 'centów'),
        bigger: this.isPriceInPLN ? 'złotych' : 'euro'
      }

      const euroString = `${converter.toWords(euro)} ${translations.bigger}`
      const centString = cent > 0 ? ` ${translations.joiner} ${converter.toWords(cent)} ${translations.smaller}` : ''

      return `${euroString}${centString}`
    },
    zlotowki: function () {
      return this.summary.grossValue * this.exchangeRate
    }
  }
}
</script>

<style scoped></style>



