<template>
  <div>
    <div class="col-3 oneliner">
      <document-text>{{ label }}</document-text>
    </div>
    <div class="col-9">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Entry',
  props: ['label']
}
</script>

<style scoped>
  .col-3 * {
    white-space: nowrap;
  }

  .oneliner {
    white-space: nowrap;
  }
</style>



